import Data from "../core/data";

const Payment_URL = "https://api.${region}.500apps.com";
class Forms extends Data {
  constructor() {
    super(Payment_URL);
  }
  // Process Payment
  async processPayment(data, plugin_id) {
    this.url = `${this.url}/forms/payment/${window.domain}/${plugin_id}`;
    let result;
    try {
      result = await this.multi_call(data);
    } catch (error) {
      result = error.message;
    }
    return result;
  }
}
export default new Forms();
