import Payment from "@/js/forms/payment";
var CryptoJS = require("crypto-js");
export default {
  // Validate the credit card/debit card in the form
  async validate(details, amount, customer_details) {
    let return_data;
    // verify all data is required
    details.first_name && details.last_name && details.card_number && details.security_code && details.card_expiration && amount
      ? // Check card length
        details.card_number.length === 16
        ? // Check CVV length
          details.security_code.length === 3
          ? // After validation it checks the app details
            (return_data = await this.afterValidate(details, amount, customer_details))
          : (return_data = "Please enter cvv numbe is valid")
        : (return_data = "Please enter 16 card number")
      : (return_data = "Please check your credit card details");
    return return_data;
  },
  // Check Stripe Account is available or not
  async afterValidate(card_details, amount, customer_details) {
    if (customer_details.plugin_id) {
      try {
        let splited_date_month = card_details.card_expiration.split("-");
        // Prepare payment payload
        let data = {
          number: this.encrypt_data(card_details.card_number),
          exp_month: splited_date_month[1],
          exp_year: splited_date_month[0],
          cvc: this.encrypt_data(card_details.security_code),
          amount: amount,
          currency: customer_details.currency,
          description: customer_details.description,
        };
        let response = await Payment.processPayment(data, customer_details.plugin_id);
        return response;
      } catch (error) {
        return error;
      }
    } else {
      return "Please connect to stripe before proceeding with payment";
    }
  },
  // Used for encrypting data
  encrypt_data(data) {
    /**
     * Ref; https://en.wikipedia.org/wiki/PBKDF2 and https://stackoverflow.com/questions/59488728/aes-encrypt-in-cryptojs-decrypt-in-pycrypto
     * first argument - random key as per the backend
     * second argument - salt key
     */
    let bytes = CryptoJS.PBKDF2("1212ad2323se4545", "6396f77306b38f54", { keySize: 48, iterations: 128 });
    // Generate hex for IV
    let iv = CryptoJS.enc.Hex.parse(bytes.toString().slice(0, 32));
    // Generate hex for key
    let key = CryptoJS.enc.Hex.parse(bytes.toString().slice(32, 96));
    // Encrypted based upon the crypto
    let ciphertext = CryptoJS.AES.encrypt(data, key, { iv: iv });
    return ciphertext.toString();
  },
};
